body {
  background-color: #010208;
  font-family: Montserrat, sans-serif;
}

.container {
  width: 100%;
  height: 100vh;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
  overflow: hidden;
}

select {
  color: rgba(255, 255, 255, .616);
  background-color: rgba(0, 0, 0, .52);
  border: 1px solid #181717;
  border-radius: 2rem;
  outline: none;
  margin-bottom: 2rem;
  padding: .5rem 2rem;
}

.content {
  z-index: 1;
  text-align: center;
  color: rgba(255, 255, 255, .616);
  width: 80rem;
  background-color: rgba(3, 5, 13, .886);
  border-radius: 2rem;
  flex-direction: column;
  gap: 1rem;
  padding: 3rem;
  display: flex;
}

img {
  border-radius: 2rem;
}

img:hover {
  opacity: .9;
}

.hidden {
  display: none;
}

.show {
  display: block;
}

.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border-radius: 50%;
  animation: animloader 1s linear infinite;
  display: inline-block;
}

@keyframes animloader {
  0% {
    box-shadow: inset -72px 0 #fff;
  }

  100% {
    box-shadow: inset 48px 0 #fff;
  }
}

